import React, { useState, useEffect } from 'react';
import {InfoSec, InfoRow, InfoColumn, TextWrapper, TopLine, Heading, Subtitle, ImgWrapper, Img, CircleWrapper, TitleBox, TimerText, SocialIcons, SocialIconLink, BootstrapText, BootstrapWrapper} from './InfoSection.elements';
import {Container, Button} from '../../globalStyles'
import {Link} from 'react-router-dom'
import { CountdownCircleTimer } from 'react-countdown-circle-timer'
import {FaTelegram, FaTwitter, FaDiscord } from 'react-icons/fa';

import AnimatedNumbers from "react-animated-numbers";

const minuteSeconds = 60;
const hourSeconds = 3600;
const daySeconds = 86400;


const timerProps = {
    isPlaying: true,
    size: 120,
    strokeWidth: 6
  };

  const renderTime = (dimension, time) => {
    return (
      <div className="time-wrapper">
        <div className="time">{time}</div>
        <div>{dimension}</div>
      </div>
    );
  };
  
  const getTimeSeconds = (time) => (minuteSeconds - time) | 0;
  const getTimeMinutes = (time) => ((time % hourSeconds) / minuteSeconds) | 0;
  const getTimeHours = (time) => ((time % daySeconds) / hourSeconds) | 0;
  const getTimeDays = (time) => (time / daySeconds) | 0;



const InfoSection = ({countdownTimer, bootstrapTimer, primary, lightBg, imgStart, lightTopLine, lightText, lightTextDesc, buttonLabel, description, headline, topLine, img, alt, start }) => {
    // start CountdownCircle  
    const stratTime = 1634991301; // 23 10 21
    const endTime = 1635721200; // 1 nov 21
    const remainingTime = endTime - stratTime;
    const days = Math.ceil(remainingTime / daySeconds);
    const daysDuration = days * daySeconds;

    // start react-animated-numbers
    const [num, setNum] = React.useState(331231);

    // start MM connect logo
    const [mmConnected, setmmConnected] = useState(false);
    useEffect(() => {
        connectWallet()
    }, []);

    window.ethereum.on('accountsChanged', (accounts) => { // listener will automatically pick up changes to accounts. We can set all switches here.
        console.log("Accounts changed triggered, new acc: ", accounts[0])

        if (accounts[0] === undefined) {
            setmmConnected(false)
        } else {
            setmmConnected(true)
        } 
      });

    const connectWallet = async () => {
        if (window.ethereum) { //check if Metamask is installed
              try {
                  const address = await window.ethereum.send('eth_requestAccounts'); //connect Metamask
                  console.log("Succesfully connected to MetaMask in infosection");
                  console.log(address)
                  setmmConnected(true)
                  const obj = {
                          connectedStatus: true,
                          status: "",
                          address: address
                      }
                      console.log("conn status: ", obj["connectedStatus"])
                      return obj;
              } catch (error) {
                  return {
                      connectedStatus: false,
                      status: " Connect to Metamask using the button on the top right."
                  }
              }
        } else {
              return {
                  connectedStatus: false,
                  status: " You must install Metamask into your browser: https://metamask.io/download.html"
              }
            } 
      };




    return (
        <>
          <InfoSec lightBg={lightBg}>
              <Container>
                  <InfoRow imgStart={imgStart}>
                      <InfoColumn>
                          <TextWrapper>


                          {countdownTimer === true ? <> <TitleBox> Public testnet release in: </TitleBox> <CircleWrapper> <CountdownCircleTimer
                              {...timerProps}
                              colors={[["#a9b3c1"]]}
                              strokeWidth="12"
                              trailColor="#101522"
                              duration={daysDuration}
                              initialRemainingTime={remainingTime}
                            >
        {({ elapsedTime }) =>
          renderTime("days", getTimeDays(daysDuration - elapsedTime))
        }
      </CountdownCircleTimer>
      <CountdownCircleTimer
        {...timerProps}
        colors={[["#a9b3c1"]]}
        strokeWidth="12"
        duration={daySeconds}
        trailColor="#101522"
        initialRemainingTime={remainingTime % daySeconds}
        onComplete={(totalElapsedTime) => [
          remainingTime - totalElapsedTime > hourSeconds
        ]}
      >
        {({ elapsedTime }) =>
          renderTime("hours", getTimeHours(daySeconds - elapsedTime))
        }
      </CountdownCircleTimer>
      <CountdownCircleTimer
        {...timerProps}
        colors={[["#a9b3c1"]]}
        strokeWidth="12"
        duration={hourSeconds}
        trailColor="#101522"
        initialRemainingTime={remainingTime % hourSeconds}
        onComplete={(totalElapsedTime) => [
          remainingTime - totalElapsedTime > minuteSeconds
        ]}
      >
        {({ elapsedTime }) =>
          renderTime("minutes", getTimeMinutes(hourSeconds - elapsedTime))
        }
      </CountdownCircleTimer>
      <CountdownCircleTimer
        {...timerProps}
        colors={[["#a9b3c1"]]}
        duration={minuteSeconds}
        strokeWidth="12"
        trailColor="#101522"
        initialRemainingTime={remainingTime % minuteSeconds}
        onComplete={(totalElapsedTime) => [
          remainingTime - totalElapsedTime > 0
        ]}
      >
        {({ elapsedTime }) =>
          renderTime("seconds", getTimeSeconds(elapsedTime))
        }
      </CountdownCircleTimer></CircleWrapper> </>: null}
                              {countdownTimer ?<> <TimerText> Join us now on social media to get started right away! </TimerText> 
                                <SocialIcons>
                            <SocialIconLink href="https://discord.gg/j3JGYthB" target="_blank" aria-label="Discord">
                                <FaDiscord />
                            </SocialIconLink>
                            <SocialIconLink href="https://twitter.com/blockgangsters" target="_blank" aria-label="Twitter">
                                <FaTwitter />
                            </SocialIconLink>
                            <SocialIconLink href="https://t.me/joinchat/hIJ1FYLDul1jNGQ0" target="_blank" aria-label="Telegram">
                                <FaTelegram />
                            </SocialIconLink>
                    </SocialIcons>
                    
                                  
                                </>  
                                   : <> <TopLine lightTopLine={lightTopLine}>{topLine}</TopLine>
                              <Heading lightText={lightText}>{headline}</Heading>
                              <Subtitle lightTextDesc={lightTextDesc}>{description}</Subtitle>
                              <Link to='/sign-up'>
                                  {mmConnected ? null : <Button big fontBig onClick={connectWallet} primary={primary}>{buttonLabel}</Button>}
                              </Link> </> }
                              {bootstrapTimer ?                       <>    <BootstrapWrapper>
      <AnimatedNumbers
        includeComma
        animateToNumber={num}
        fontStyle={{ fontSize: 30 }}
        configs={[
          { mass: 1, tension: 220, friction: 100 },
          { mass: 1, tension: 180, friction: 130 },
          { mass: 1, tension: 280, friction: 90 },
          { mass: 1, tension: 180, friction: 135 },
          { mass: 1, tension: 260, friction: 100 },
          { mass: 1, tension: 210, friction: 180 },
        ]}
      ></AnimatedNumbers></BootstrapWrapper><BootstrapText> / 1,000 bootstrap slots used </BootstrapText></> : null}
                          </TextWrapper>
                      </InfoColumn>

                      <InfoColumn>
                          <ImgWrapper start={start}>
                              <Img src={img} alt={alt} />
                          </ImgWrapper>
                      </InfoColumn>
                  </InfoRow>
              </Container>
            </InfoSec>  
        </>
    )
}

export default InfoSection
