import React, { useState, useEffect } from 'react';
import {Nav, NavbarContainer, NavLogo, NavIcon, MobileIcon, NavMenu, NavLinks, NavItem, NavItemBtn, NavBtnLink, StatsItem, StatsContainer, MMConnect } from './Navbar.elements';
import { FaBars, FaTimes } from 'react-icons/fa';
import {IconContext} from 'react-icons/lib'
import {Button} from '../../globalStyles'
import NumberFormat from "react-number-format";
import {connectWallet}  from '../EthFunctions'

import {StateContext} from '../../App';



const Navbar = () => {

    const [click, setClick] = useState(false)
    const [button, setButton] = useState(true);

    const [balance, gangBalance, mmConnected, mainConnected, testConnected, adminConnected] = React.useContext(StateContext);

    
    

    const handleClick = () => setClick(!click)
    const closeMobileMenu = () => setClick(false)

    const showButton = () => {
        if (window.innerWidth <= 960) {
            setButton(false)
        } else {
            setButton(true)
        }
    }

    useEffect(() => {
        showButton()
    }, []);

    window.addEventListener('resize', showButton);


    
    return (
        <IconContext.Provider value={{color: '#fff'}}> 
            <Nav>
                <NavbarContainer>
                    <NavLogo to="/" onClick={closeMobileMenu}>
                        <NavIcon src={require("../../images/logo_gang_101522.jpg").default} to="/" />

                        Blockgangsters.io

                    </NavLogo>

                    <MobileIcon onClick={handleClick}>
                        {click ? <FaTimes /> : <FaBars />}
                    </MobileIcon>
                    
                    <NavMenu onClick={handleClick} click={click}>
                    <NavItem>
                    <NavLinks to='/helppage'>

                                <h6 style={{color: "red"}}> Beta live on Polygon testnet! Click here for guide</h6>
                                </NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to='/'>
                                Home
                            </NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to='/whitepaper'>
                                Whitepaper
                            </NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to='/contract'>
                                Contract
                            </NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to='/roadmap'>
                                Roadmap
                            </NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to='/tokenomics'>
                                Tokenomics
                            </NavLinks>
                        </NavItem>  
                            {adminConnected ?                         
                            <NavItem>
                                <NavLinks to='/admin'>
                                Admin page
                                </NavLinks>
                            </NavItem>  : null}
                        
 
                            {button && !mmConnected ? 
                                <NavItemBtn><NavBtnLink to="/sign-up"> 
                                <Button primary onClick={() => {
                                    connectWallet()
                                    
                                }}>Connect MetaMask</Button>
                        </NavBtnLink></NavItemBtn>  : [
                                        (!button && !mmConnected 
                                            ?
                                            <NavItemBtn><NavBtnLink to="/sign-up">
                                                <Button onClick={() => {
                                                    connectWallet()
                                                    
                                                    }} fontBig primary>Connect MetaMask</Button>
                                                </NavBtnLink> </NavItemBtn>
                                            : null ) ]
                        }
                        {mmConnected && mainConnected === true ? (
                        <StatsContainer>
                            <StatsItem>
                                <NumberFormat
                                    value={balance}
                                    displayType={"text"}
                                    decimalSeparator={"."}
                                    thousandSeparator={true}
                                    decimalScale={4}
                                    prefix={" Balance: "}
                                    />
                            </StatsItem>          
                                
                            <StatsItem>
                                <NumberFormat 
                                    value={gangBalance}
                                    displayType={"text"}
                                    decimalSeparator={"."}
                                    thousandSeparator={true}
                                    decimalScale={4}
                                    prefix={" Balance: "} />
                            </StatsItem>
                        </StatsContainer> ) : [ (mmConnected && mainConnected !== true && testConnected === true ? 
                                                <StatsContainer>
                                                <StatsItem>
                                                    <NumberFormat
                                                        value={balance}
                                                        displayType={"text"}
                                                        decimalSeparator={"."}
                                                        thousandSeparator={true}
                                                        decimalScale={4}
                                                        prefix={" Balance: "}
                                                        />
                                                </StatsItem>          
                                                    
                                                <StatsItem>
                                                    <NumberFormat 
                                                        value={gangBalance}
                                                        displayType={"text"}
                                                        decimalSeparator={"."}
                                                        thousandSeparator={true}
                                                        decimalScale={0}
                                                        prefix={" Balance: "} />
                                                </StatsItem>
                                                <MMConnect>Testnet connected</MMConnect>
                                            </StatsContainer>  : (mmConnected ? <MMConnect>Unsupported chain</MMConnect> : null))]
                        }
                    </NavMenu>
                </NavbarContainer>
            </Nav>
        </IconContext.Provider>
    )
}

export default Navbar
